"use client";
import Image from "next/image";
import React, { useState, useEffect } from "react";
import btc from "../assets/btc.svg";
import bg from "../assets/bg.png"; // Make sure to import your background image
import Button from "./Button";

function Navbar() {
  const [isScrolled, setIsScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 0) {
        setIsScrolled(true);
      } else {
        setIsScrolled(false);
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <div className="fixed top-0 left-0 right-0 w-full z-50">
      {/* Background Image Container */}
      <div className="absolute inset-0 w-full h-full -z-10">
        <Image
          src={bg}
          alt="Background"
          fill
          className="object-cover opacity-[0.08]"
          priority
        />
      </div>

      {/* Navbar Content */}
      <div
        className={`w-full transition-all duration-300 ${isScrolled
          ? "bg-black/30 backdrop-blur-md"
          : "bg-transparent"}`}
      >
        <div className="p-5 flex justify-between max-w-7xl mx-auto">
          <div className="flex space-x-1 items-center">
            <p className="text-[30px] font-medium">Re</p>
            <Image
              src={btc}
              alt="btc"
              width={21}
              height={21}
              className="h-[50px] w-[50px]"
            />
          </div>
          <div className="flex gap-8">
            <Button text="H O M E" />
            <Button text="A B O U T" />
            <Button text="C O N N E C T  W A L L E T" />
          </div>
        </div>
      </div>
    </div>
  );
}

export default Navbar;
